import React from "react";
import { createRoot } from "react-dom/client"; // Mise  jour de l'importation pour utiliser createRoot
import App from "./App";
import "./styles/index.scss";

// REDUX
import { Provider } from "react-redux";
import { configureStore } from "@reduxjs/toolkit";
import rootReducer from "./reducer";

const store = configureStore({
  reducer: rootReducer,
  devTools: true,
});

const container = document.getElementById("root"); // Slectionner l'lment racine
const root = createRoot(container); // Crer une racine pour l'application

// Utiliser root.render pour monter l'application React
root.render(
  <Provider store={store}>
    <App />
  </Provider>
);
